import React from 'react'
import style from './style.module.css'

class Intro extends React.Component {
  render() {
    return (
      <div className={style.intro}>
        <h2><a href="https://www.womenshealthmag.com/weight-loss/a19434332/what-is-the-keto-diet/">Keto</a> recipes and dishes in NYC, from our table to yours</h2>
      </div>
    )
  }
}

export default Intro



